import React, { Component } from 'react';
import styled from "styled-components";
import AboutMeTitle from './AboutMeTitle.js';
import ProfileBox from './ProfileBox.js';
import AssistBox from './AssistBox.js';

const AboutMeStyle = styled.div`
    width: 100%;
    height: 100%;
    margin-top: 60px;
    overflow: hidden;

    @media (min-width:681px) {
        display: flex;
    }
`;

const AboutmeProfileLocation = styled.div `
    width: 100%;
    height: 100%;

    @media (min-width:0px) and (max-width: 680.9px) {
        width: 214px;
        height: 100%;
        position: absolute;
        left: 50%;
        margin-left: -107.5px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const AssistMent = styled.div `
    width: 100%;
    height: 100%;

    /*해상도*/
    @media (min-width:0px) and (max-width:374.9px) {
        font-size: 15px;
        line-height: 10px;
        margin-top: 300px;
    }

    @media (min-width:375px) and (max-width:424.9px) {
        font-size: 15px;
        line-height: 10px;
        margin-top: 300px;
    }

    @media (min-width:425px) and (max-width:549.9px) {

        font-size: 15.5px;
        line-height: 10px;
        margin-top: 300px;
    }

    @media (min-width:550px) and (max-width:680.9px) {
        font-size: 15.5px;
        line-height: 10px;
        margin-top: 300px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-top: 224px;
        margin-left: 0px;

        font-size: 16px;
        line-height: 15.2px;
    }

    @media (min-width:768px) and (max-width:865.9px) {
        margin-top: 220px;
        margin-left: 0px;

        font-size: 18px;
        line-height: 15px;
    }

    @media (min-width: 866px) and (max-width: 1679.9px) {
        margin-top: 210px;
        margin-left: 0px;

        font-size: 20px;
        line-height: 16px;
    }

    @media (min-width:1680px) and (max-width:1919.9px) {
        margin-top: 210px;
        margin-left: 10%;
        
        font-size: 20px;
        line-height: 16px;
    }

    @media (min-width:1920px) {
        margin-top: 210px;
        margin-left: 15%;

        font-size: 20px;
        line-height: 16px;
    }
`;

class AboutMeBox extends Component {
    render() {
        return (
        <div>
            <AboutMeStyle>
                <AboutmeProfileLocation>
                    <div>
                        <AboutMeTitle></AboutMeTitle>
                        <ProfileBox></ProfileBox>
                    </div>
                </AboutmeProfileLocation>
                <AssistMent>
                    <div><AssistBox></AssistBox></div>
                </AssistMent>
            </AboutMeStyle>
        </div>
        )
    }
}

export default AboutMeBox;