import React, { Component } from 'react';
import styled from "styled-components";
import SkillTitle from './SkillTitle.js';
import LevelBox from './LevelBox.js';

const SkillStyle = styled.div`
    width: 100%;
    height: 100%;
    margin-top: 60px;
    overflow: hidden;
    
`;

const SkillLevelLocation = styled.div `
    height: 100%;
    /*해상도*/
    @media (min-width:0px) and (max-width:374.9px) {
        margin-left: 50px;
    }

    @media (min-width:375px) and (max-width:424.9px) {
        margin-left: 78px;
    }

    @media (min-width:425px) and (max-width:549.9px) {
        margin-left: 105px;
    }

    @media (min-width:0px) and (max-width:680.9px) {
        position: absolute;
        width: 194px;
        left: 50%;
        margin-left: -97px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const LevelbarLocation = styled.div `

    /*해상도*/
    @media (min-width:0px) and (max-width:374.9px) {
        margin-top: 60px;
        margin-left: 50px;

        line-height: 16px;
    }

    @media (min-width:375px) and (max-width:424.9px) {
        margin-top: 60px;
        margin-left: 78px;

        line-height: 16px;
    }

    @media (min-width:425px) and (max-width:549.9px) {
        margin-top: 60px;
        margin-left: 105px;

        line-height: 16px;
    }

    @media (min-width:550px) and (max-width:680.9px) {
        margin-top: 60px;
        margin-left: 167px;

        line-height: 18px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-top: 120px;
        margin-left: 0px;

        line-height: 18px;
    }

    @media (min-width:768px) and (max-width:865.9px) {
        margin-top: 120px;
        margin-left: 0px;

        line-height: 18px;
    }

    @media (min-width: 866px) and (max-width: 1679.9px) {
        margin-top: 120px;
        margin-left: 0px;

        line-height: 18px;
    }

    @media (min-width:1680px) and (max-width:1919.9px) {
        margin-top: 120px;
        margin-left: 10%;

        line-height: 16px;
    }

    @media (min-width:1920px) {
        margin-top: 120px;
        margin-left: 15%;

    }
`;

const Flex = styled.div`
        display: flex;
`;

const Bar = styled.div`
    width: 100px;
    height: 15px;
    background: linear-gradient(to right, blue, green, red);
    border-radius: 3px 3px 3px 3px;

    
`;

const Color = styled.div`
    margin-top: 5px;
    color : #eaeaea;
    font-size: 11.3px;
    font-family: 'Noto Sans KR', sans-serif;
`
const Color2 = styled.div`
    font-size: 19px;
    color: #FBD46D;
    margin-top: 40px; 
    margin-left: 20px;
`

class SkillBox extends Component {
    render() {
        return (
        <div>
            <SkillStyle>
                <SkillLevelLocation>
                    <div>
                        <SkillTitle></SkillTitle>
                        <LevelBox></LevelBox>
                    </div>
                </SkillLevelLocation>
            </SkillStyle>
            
        </div>
        );
    }
}

export default SkillBox;