import React, { Component } from 'react';
import styled from "styled-components";
import SkillPlatformBox from './SkillPlatformBox.js';
import C from './img/c.png';
import Cshop from './img/cshop.png';
import Java from './img/java.png';
import Python from './img/python.png';
import Html from './img/html.png';
import Css from './img/css.png';
import Js from './img/js.png';
import Mysql from './img/mysql.png';
import Oracle from './img/oracle.png';
import Androidstudio from './img/androidstudio.png';
import Reactt from './img/react.png';
import Arduino from './img/arduino.png';

const SkillStyle = styled.div`
    width: 100%;
    height: 100%;

    @media (min-width:0px) and (max-width: 680.9px) {
        margin-top: 170px;
    }
`;

const ItemBoxStyle = styled.div `
    height: 100%;
    transition: 1s;
    margin-top: 30px;
    @media (min-width:0px) and (max-width:680.9px) {
        width: 100%;
        margin-left: 0px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
    }
    
    @media (min-width:768px) and (max-width:1023.9px) {
        margin-left: 100px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
    }
    
    @media (min-width: 1600px) {
        margin-left: 20.3%;
    }
`;

const Flex = styled.div `
    @media (min-width: 874.9px) {
        width: 100%;
        display: flex;
    }
`;

class SkillItemBox extends Component {
    Platform1= [{
        Smargin: "0px",
        Title:'Language',
        Border: "1vw",
        Lmargin: "-15px",
        Img1: <img src={Java} alt='' />,
        Img2: <img src={Python} alt='' />,
        Img3: <img src={C} alt='' />,
        Img4: <img src={Cshop} alt='' />,
        
        Text1: "Java",
        Text2: "Python",
        Text3: "C",
        Text4: "C#",
        Color1: "red",
        Color2: "red",
        Color3: "green",
        Color4: "blue"
    },{
        Smargin: "15px",
        Title:'Web Skills',
        Border: "1vw",
        Lmargin: "-15px",
        Img1: <img src={Html} alt='' />,
        Img2: <img src={Css} alt='' />,
        Img3: <img src={Js} alt='' />,
        Text1: "HTML",
        Text2: "CSS",
        Text3: "JavaScript",
        Color1: "green",
        Color2: "blue",
        Color3: "blue",
        Display1: "''",
        Display2: "''",
        Display3: "none"
    },{
        Smargin: "15px",
        Title:'Database',
        Border: "1vw",
        Lmargin: "0px",
        Img1: <img src={Mysql} alt='' />,
        Img2: <img src={Oracle} alt='' />,
        Text1: "MySQL",
        Text2: "ORACLE",
        Color1: "green",
        Color2: "green",
        Display1: "''",
        Display2: "none",
        Display3: "none"
    },{
        Smargin: "15px",
        Title:'IDE & Library',
        Border: "1vw",
        Lmargin: "-15px",
        Img1: <img src={Androidstudio} alt='' />,
        Img2: <img src={Reactt} alt='' />,
        Img3: <img src={Arduino} alt='' />,
        Text1: "AndroidStudio",
        Text2: "React",
        Text3: "ARDUINO",    
        Color1: "green",
        Color2: "green",
        Color3: "blue",
        Display1: "''",
        Display2: "''",
        Display3: "none"
    }
]

renderPlatforms1 = (arr) => {
    return arr.map(a=> {
        return <SkillPlatformBox Smargin={a.Smargin} Title={a.Title} Border={a.Border} Lmargin = {a.Lmargin} 
        Img1={a.Img1} Img2={a.Img2} Img3={a.Img3} Img4={a.Img4} Img5={a.Img5} Img6={a.Img6}
        Text1={a.Text1} Text2={a.Text2} Text3={a.Text3} Text4={a.Text4}
        Color1={a.Color1} Color2={a.Color2} Color3={a.Color3} Color4={a.Color4} 
        Display={a.Display1} Display2={a.Display2} Display3={a.Display3} key={a.Title} />
    })
}
    render() {
        return (
        <div>
            <SkillStyle>
                <ItemBoxStyle>
                    <Flex>
                        {this.renderPlatforms1(this.Platform1)}
                    </Flex>
                </ItemBoxStyle>
            </SkillStyle>
        </div>
        );
    }
}

export default SkillItemBox;