import React, { Component } from 'react';
import styled from "styled-components";


const PlatformStyle = styled.div`
    width: 15vw;
    height: 350px;
    margin-left: ${props => props.Smargin};
    background-color: #fffff6;
    border-radius: 10px 10px 10px 10px;

    @media (min-width:0px) and (max-width:680.9px) {
        width: 70%;
        margin-left: 15%;
    }


    @media (min-width:681px) and (max-width:874.9px) {
        width: 35vw;
        margin-left: 0px;
    }
    

    @media (min-width:875px) and (max-width:1023.9px) {
        width: 19vw;
    }

    @media (min-width:1024px) and (max-width:1230.9px) {
        width: 16vw;
    }

    @media (min-width:1231px) {
    }

`;

const PlatformTitle = styled.div `
    width: 100%;
    margin-top: 0px;
    color: black;
    font-size: 28px;
    text-align: center;
    line-height: 50%;
    font-family: 'Play', sans-serif;
    @media (min-width:875px) and (max-width:1100px) {
        font-size: 25px;
    }
`;

const Img = styled.div `
    width: 60px;
    height: 40px;
    margin-left: 10px;
    overflow: hidden; 
`

const MentStyle = styled.div`
    font-size: 15px;
    margin-left: -15px;
    font-family: 'Noto Sans KR', sans-serif;
    color: ${props => props.Color};
`
const Flex = styled.div`
    display: Flex;
`  
const td = styled.div`
    border-bottom: 1pt solid black;
`;

const Hr1 = styled.div`
    width: 92%;
    margin-left: 4%;
    text-align:center;
    margin-top: -15px;
    border: none;
    height: 1px;
    /* Set the hr color */
    color: blue; /* old IE */
    background-color: blue; /* Modern Browsers */
    display: ${props => props.Display1};
    opacity 0.1;
`

const Hr2 = styled.div`
    width: 92%;
    margin-left: 4%;
    text-align:center;
    margin-top: -15px;
    border: none;
    height: 1px;
    /* Set the hr color */
    color: blue; /* old IE */
    background-color: blue; /* Modern Browsers */
    display: ${props => props.Display2};
    opacity 0.1;
`

const Hr3 = styled.div`
    width: 92%;
    margin-left: 4%;
    text-align:center;
    margin-top: -15px;
    border: none;
    height: 1px;
    /* Set the hr color */
    color: blue; /* old IE */
    background-color: blue; /* Modern Browsers */
    display: ${props => props.Display3};
    opacity 0.1;
`

const Color1 = styled.div`
    
`;

class SkillPlatformBox extends Component {
    render() {
        return (
        <div>
            <PlatformStyle Smargin={this.props.Smargin} Border={this.props.Border} Height={this.props.Height}>
                <table width="100%" height="100%">
                    <tbody>
                        <tr height="5%">
                            <td colspan="2">
                                <PlatformTitle style={{lineHeight: "60px"}}>{this.props.Title}</PlatformTitle>
                            </td>
                        </tr>

                        <tr>
                            <td width="30px">
                                <Img>{this.props.Img1}</Img>
                            </td>
                            <td><MentStyle Color={this.props.Color1}><b>{this.props.Text1}</b></MentStyle></td>
                        </tr>
                        <tr height="1%">
                            <td colspan="2"><Hr1 Display1={this.props.Display1}><hr></hr></Hr1></td>
                        </tr>
                        <tr>
                            <td>
                                <Img>{this.props.Img2}</Img>
                            </td>
                            <td><MentStyle Color={this.props.Color2}><b>{this.props.Text2}</b></MentStyle></td>
                        </tr>
                        <tr height="1%">
                            <td colspan="2"><Hr2 Display2={this.props.Display2}><hr></hr></Hr2></td>
                        </tr>
                        <tr>
                            <td>
                                <Img>{this.props.Img3}</Img>
                            </td>
                            <td><MentStyle Color={this.props.Color3}><b>{this.props.Text3}</b></MentStyle></td>
                        </tr>
                        <tr height="1%">
                            <td colspan="2" ><Hr3 Display3={this.props.Display3}><hr></hr></Hr3></td>
                        </tr>
                        <tr>
                            <td>
                                <Img>{this.props.Img4}</Img>
                            </td>
                            <td><MentStyle Color={this.props.Color4}><b>{this.props.Text4}</b></MentStyle></td>
                        </tr>
                    </tbody>
                </table>
            </PlatformStyle>
            <br></br>
        </div>
        );
    }
}
export default SkillPlatformBox;
