import React, { Component } from 'react';
import styled from "styled-components";
import MenuButton from './MenuButton.js';

const MenubarStyle = styled.div `
    width: 100%;
    height: 80px;
    position: fixed;
`;

const MenubarLocation = styled.div`
    display: flex;
    width: 100%;
    height: 100%; 
    /*해상도*/
    @media (min-width:0px) and (max-width: 1023.9px) {
        justify-content: center; align-items: center;
    }
    
    @media (min-width: 1024px) and (max-width:1599.9px) {
        margin-left: 300px;
    }

    @media (min-width: 1600px) {
        margin-left: 20%;
    }
`;

class Menubar extends Component {
    menus= [{
            text:'ABOUT ME',
            rmargin: "15px"
        },{
            text:'SKILL',
            rmargin: "15px"
        },{
            text:'PORTFOLIO',
            rmargin: "15px"
        },{
            text:'CONTACT',
            rmargin: "15px"
        }
    ]

    renderMenus = (arr) => {
        return arr.map(a=> {
            return <MenuButton text={a.text} rmargin={a.rmargin} key={a.text}/>
        })
    }
    render() {
        return ( 
        <div>
            <MenubarStyle>
                <MenubarLocation>{this.renderMenus(this.menus)}</MenubarLocation>
            </MenubarStyle>
        </div>
        );
    }
}

export default Menubar;