import React, { Component } from 'react';
import styled from "styled-components";

const TitleStyle = styled.div `
    width: 215px;
    margin-bottom: 30px;
    font-size: 61px;    
    color: white;
    font-family: 'Nunito', sans-serif;
    

    
`;
const HrStyle = styled.div `
    width: 86px;
    margin-top: -30px;
    border: 0;
    height: 3px;
    background-color: #a6a6a6;
`;

class AboutMeTitle extends Component {
    render() {
        return (
        <div>
            <TitleStyle>
                ABOUT ME
                <HrStyle><hr color= "#a6a6a6"></hr></HrStyle>
            </TitleStyle>
        </div>
        );
    }
}

export default AboutMeTitle;