import React, { Component } from 'react';
import styled from "styled-components";

const ButtonStyle = styled.div `
    width: 100%;
    height: 80px;
    margin-right: ${props => props.rmargin};
    float: left;
    text-align: center;
    color: #eeeeee;
    cursor: pointer;
    font-family: 'Nunito', sans-serif;
    transition: all 0.3s;
    font-size: 15px;
    &:hover {
        transform: scale(1.1);
    }

    /*해상도*/
    @media (min-width:0px) and (max-width: 325px) {
        font-size: 14px;
    }

`;


class MenuButton extends Component {
    render() {
        return ( 
        <div>
            <ButtonStyle rmargin={this.props.rmargin}>   
                <span style={{textAlign: "center", lineHeight: "80px" }}>{this.props.text}</span>
            </ButtonStyle>
        </div>
        );
    }
}

export default MenuButton;