import React, { Component } from 'react';
import styled, {keyframes} from 'styled-components';

const Subject = styled.div `
    width: 100%;
    height: 100%;
    color: white;
    font-family: 'Oxygen', sans-serif;
`;

const Introduction = styled.div `
    width: 100%;
    height: 100%;
    padding-top: 2.5%;
    color: #ffffff;
    /*해상도*/
    @media screen and (min-width: 0px) and (max-width:767.9px) {
        font-size: 20px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439.9px) {
        font-size: 25px;
    }

    @media screen and (min-width: 1440px) {
        font-size: 30px;
    }
`;

const Mouse = styled.div`
    position: absolute;
    width: 30px;
    height: 50px;
    border: 5px solid #ffffff;
    border-radius: 20px;
    left: 50%;
    top: 85%;
    margin-left: -20px;
`;

const Amouse = keyframes`
0%{
    top:5px;
    opacity: 1;
}
100% {
    top: 20px;
    opacity: 0;
    }
`;

const MouseAfter = styled.div`
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    background: #ffffff;
    top: 8px;
    left: 50%;
    matgin-top: -10%;
    margin-left: -4px;
    border-radius: 50%;
    animation: ${Amouse} 2s infinite;
    opacity: 1;
    
`;


class HomeMent extends Component {
    render() {
        return (
        <div>
            <Subject>
                <b>{this.props.subject}</b>
            </Subject>

            <Introduction>
                {this.props.introduction}
            </Introduction>
            <Mouse>
                <MouseAfter></MouseAfter>
            </Mouse>
        </div>
        );
    }
}

export default HomeMent;