import React, { Component } from 'react';
import styled from "styled-components";
import LevelItem from './LevelItem.js';
import Levelinformation from './img/Levelinformation.png'

const LevelStyle = styled.div `
    width: 100%;
    font-family: 'Noto Sans KR', sans-serif;
    display: flex;
    @media (min-width:0px) and (max-width:680.9px) {
        font-size: 15.9px;
    }
`;

const Red = styled.div`
    color: red;
`

const Green = styled.div`
    color: green;
`

const Blue = styled.div`
    color: blue;
`

const Information = styled.div`
    width: 24px;
    height: 24px;
    background-image: url(${Levelinformation});
`

class LevelBox extends Component {
    level= [{
        text:'■ Lv3',
        fcolor:'red'
        },{
        text:'■ Lv2',
        fcolor:'green'
        },{
        text:'■ Lv1',
        fcolor:'blue'
        }
    ]

    renderLevel = (arr) => {
        return arr.map(a=> {
            return <LevelItem text={a.text} fcolor={a.fcolor}/>
        })
    }

    render() {
        return (
        <div>
            <LevelStyle>
            <Red>■ Lv3</Red>&nbsp;&nbsp;&nbsp;<Green>■ Lv2</Green>&nbsp;&nbsp;&nbsp;<Blue>■ Lv1</Blue>
                &nbsp;&nbsp;&nbsp;&nbsp;<Information></Information>
            </LevelStyle>
        </div>
        );
    }
}

export default LevelBox;        