import React, { Component } from 'react';
import styled from "styled-components";

const TitleStyle = styled.div`
    width: 50px;
    margin-bottom: 30px;
    font-size: 61px;
    color: white;
    font-family: 'Nunito', sans-serif;
`;

const HrStyle = styled.div `
    width: 193px;
    margin-top: 0px;
    border: 0;
    height: 3px;
    color: #eeeeee;
    background: #eeeeee;
`;

class SkillTitle extends Component {
    render() {
        return (
        <div>
            <TitleStyle>
                SKILLs
                <HrStyle></HrStyle>
            </TitleStyle>
        </div>
        );
    }
}

export default SkillTitle;