import React, { Component } from 'react';
import styled from "styled-components";
import HomeMent from './HomeMent.js';
import tmp from '../Home/tmp.jpg';

const HomeStyle = styled.div `
    width: 100%;   
    height: 100vh;
    margin-top: -43px;
    position: relative;
    text-align: center;
    background-image: linear-gradient( rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),url(${tmp});
    background-attachment: fixed;
    background-size: cover;   
`;

const MentLocation = styled.div`
    width: 100%;
    height: 100%;

    /*해상도*/
    @media screen and (min-width: 0px) and (max-width: 374.9px) {
    padding-top: 30vh;
    font-size: 46px;
    }

    @media screen and (min-width: 375px) and (max-width: 424.9px) {
        padding-top: 30vh;
        font-size: 55px;
    }

    @media screen and (min-width: 425px) and (max-width: 767.9px) {
        padding-top: 30vh;
        font-size: 61px;
    }

    @media screen and (min-width: 768px) and (max-width: 1439.9px) {
        padding-top: 40vh;
        font-size: 63px;
    }

    @media screen and (min-width: 1440px) {
        padding-top: 40vh;
        font-size: 90px;
    }
`;

class HomeBox extends Component {
    render() {
        return (
        <div> 
            <HomeStyle>
                <MentLocation>
                    <HomeMent subject={"Developer Jun-Seop Shin"} introduction={"Thank you for visiting my portfolio ."}></HomeMent>
                </MentLocation>
            </HomeStyle>
        </div>
        );
    }
}

export default HomeBox;