import React, { Component } from 'react';
import styled from "styled-components";

const AssistStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #FBD46D;
    text-decoration: none;
    transition: 1s;
    text-shadow: 0 0 .1em, 0 0 .3em; 
    

    @media (min-width:0px) and (max-width:681px) {
        width: 220px;
        position: absolute;
        left: 50%;
        margin-left: -108px;
    }
`;

class AssistBox extends Component {  
    render() {
        return (
        <div>
            <AssistStyle>
                <p>새롭고 창의적인 것을 추구하며,</p>
                <p>커뮤니케이션을 중요시합니다.</p>
            </AssistStyle>
        </div>
        )
    }
}

export default AssistBox;