import React, { Component } from 'react';       
import styled from 'styled-components';
import VisionMent from './VisionMent.js';

const VisionStyle = styled.div`
    overflow: hidden;
`;


class VisionBox extends Component {
    render() {
        return (
        <div>
            <VisionStyle>
                <VisionMent></VisionMent>
            </VisionStyle>
        </div>
        );
    }
}

export default VisionBox;