import React, { Component } from 'react';
import styled from "styled-components";


const LiStyle = styled.div`
    font-size: 17px;
    margin-top: -10px;
    margin-left: -40px;
    list-style: none;
    font-weight: normal;
`;


class LevelItem extends Component {
    render() {
        return (
        <div>
            <ul>
                <LiStyle>
                    <li style={{color:this.props.fcolor}}>{this.props.text}</li>
                    ■ Lv3 ■ Lv2 ■ Lv1
                </LiStyle>
            </ul>
        </div>
        );
    }
}

export default LevelItem;